.BillMenu {
  display: flex;
  width: 100%;
}

.BillMenu .name {
  flex: 2;
}

.BillMenu .price {
  flex: 1;
}

.BillMenu .count {
  flex: 1;
}

.BillMenu .return {
  margin-left: auto;
  color: #f06595;
  cursor: pointer;
}

.BillMenu .return:hover {
  text-decoration: underline;
}

.BillMenu+.BillMenu {
  margin-top: 1rem;
}
.StoreMenu {
  background: white;
  border: 1px solid #495057;
  padding: 0.5rem;
  border-radius: 2px;
  cursor: pointer;
}

.StoreMenu h4 {
  margin-top: 0;
  margin-bottom: 1rem;
}

.StoreMenu:hover {
  background: #495057;
  color: white;
}

.StoreMenu+.StoreMenu {
  margin-top: 1rem;
}
.StoreTemplate {
  width: 768px;
  display: flex;
  border: 1px solid black;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
}

.StoreTemplate h2 {
  margin-top: 0;
}

.StoreTemplate>div {
  padding: 1rem;
  flex: 1;
}

.StoreTemplate .items-wrapper {
  background: #f8f9fa;
}